<template>
    <header class="header">

        <nav v-if="navbar" class="nav">
            <button @click="switchLang" class="nav__lang">{{ $i18n.locale }}</button>
            <h1 class="nav__title">{{ $t('notes') }}</h1>
            <button class="nav__search" @click="navbar = false">
                <img src="@/assets/img/search.svg" alt="">
            </button>
        </nav>

        <nav v-else class="nav__search">
            <button @click=" navbar = true"><img src="@/assets/img/back.png" alt="back"></button>
            <input v-model="search" type="text" :placeholder="$t('searchInput')" autofocus>
            <button><img src="@/assets/img/clear.png" alt="clear" @click="search = ''"></button>
  
        </nav>

    </header>
</template>

<script>
export default {
    data() {
        return {
            navbar: true,
            search: ''
        }

    },
    methods: {
        switchLang() {
            this.$i18n.locale == 'ru' ? this.$i18n.locale = 'eng' : this.$i18n.locale = 'ru'

            localStorage.lang = this.$i18n.locale
        }
    },
    watch: {
        search(newVal) {
            this.$emit('search', newVal)
        }
    }

}
</script>
